import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Tab, Tabs } from 'react-bootstrap';

import CodeSnippetBlock from '../CodeSnippetBlock';

import classes from './TabbedCodeSnippet.module.scss';

const tabTitle = (language) => ({
  html: 'Html',
  react: 'React',
})[language];

const TabbedCodeSnippet = ({ codes }) => {

  const [active, setActiveTab] = useState('html');

  const tabs = codes.map((code) => (
    <Tab eventKey={code.language} title={tabTitle(code.language)} key={code.language}>
      <CodeSnippetBlock value={code.value} language={code.language} theme={{ emptyCard: classes.emptyCard }}/>
    </Tab>
  ));

  return (
    <div className={classes.container}>
    <Tabs activeKey={active} onSelect={(tab) => { console.log(tab);setActiveTab(tab)}} className={classes.tabs} >
      {tabs}
    </Tabs>
    </div>
  );
};

TabbedCodeSnippet.propTypes = {
  codes: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string,
      language: PropTypes.string,
    }),
  ).isRequired,
};

export default TabbedCodeSnippet;
