import React from 'react';
import { Form } from 'react-bootstrap';

const Checkbox = ({
  label,
  value,
  defaultChecked,
  onChange
}) => {
  return (
    <Form.Group controlId={value}>
      <Form.Check type="checkbox" label={label} defaultChecked={defaultChecked} onChange={onChange} />
    </Form.Group>
  );
};

export default Checkbox;
