import React, { useState } from 'react';
import { Container, Row, Col, Navbar } from 'react-bootstrap';
import Script from 'react-load-script';
import { WIDGET_SCRIPT_URL } from '../../constants';

import LeftNavMenu from '../LeftNavMenu';
import SearchWidget from '../SearchWidget';
import TabbedCodeSnippet from '../TabbedCodeSnippet';
import classes from './Layout.module.scss';


const Layout = () => {
  const [isScriptLoaded, setScriptLoaded] = useState(false);
  return (
    <>
    <Navbar bg="light" variant="light" className={classes.bottomShadow}>
      Shutterstock UI Wizard
    </Navbar>
    <Container fluid={true} style={{ padding: 0}}>
      <Row noGutters>
        <Col sm="12" lg="2" className={classes.leftColumn}>
          <LeftNavMenu />
        </Col>
        <Col sm="12" lg="10">
          <Row>
            <Col>
              <TabbedCodeSnippet />
            </Col>
          </Row>
          <Row>
            <Col>
            {isScriptLoaded && <SearchWidget />}
            <Script
              url={WIDGET_SCRIPT_URL}
              onLoad={() => setScriptLoaded(true)}
            />
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
    </>
  );
};

export default Layout;
