import { useEffect, useRef, useCallback } from 'react';
// import { useDispatch, useSelector } from 'react-redux';


const useSearchWidget = ({
  apiKey,
  assetsPerPage,
  dynamicSubtitle,
  dynamicTitle,
  htmlElement,
  mediaType,
  showItemId,
  showMore,
  showSearchBar,
  widgetSubtitle,
  widgetTitle,
}) => {
  const widgetInstanceRef = useRef();
  const widgetRef = useRef();

  const configureWidget = useCallback(() => {
    const widgetConfig = {
      mediaType,
      title: widgetTitle,
      subtitle: widgetSubtitle,
      container: widgetRef.current,
      showMore,
      key: apiKey,
      showItemId,
      dynamicTitle,
      dynamicSubtitle,
      showSearchBar,
      assetsPerPage,
    };

    // eslint-disable-next-line no-undef
    if (typeof window === 'object' && window.ShutterstockWidget) {
      // eslint-disable-next-line no-undef
      const widgetInstance = new window.ShutterstockWidget(widgetConfig);
      widgetInstance.search({
        query: '',
      });
      widgetInstanceRef.current = widgetInstance;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    apiKey,
    assetsPerPage,
    dynamicSubtitle,
    dynamicTitle,
    htmlElement,
    mediaType,
    showItemId,
    showMore,
    showSearchBar,
    widgetSubtitle,
    widgetTitle,
  ]);

  useEffect(configureWidget, [
    apiKey,
    assetsPerPage,
    dynamicSubtitle,
    dynamicTitle,
    htmlElement,
    mediaType,
    showItemId,
    showMore,
    showSearchBar,
    widgetSubtitle,
    widgetTitle,
  ]);

  return {
    widgetInstanceRef,
    widgetRef,
  };
};

export default useSearchWidget;
