const intialState = {
  mediaType: 'images',
  inputs: {
    apiKey: '0OviQeVft2KY9BiFNJF8XRPvTe3ZXUFS',
    htmlElement: 'widget-container',
    widgetSubtitle: '',
    widgetTitle: 'Shutterstock UI Search Widget',
    assetsPerPage: 26,
  },
  booleans: {
    dynamicSubtitle: true,
    dynamicTitle: true,
    showMore: true,
    showSearchBar: true,
  }
};


export default (prevState = { ...intialState }, action) => {
 switch (action.type) {
  case 'SIMPLE_ACTION':
   return {
    result: action.payload
   }

 case 'SET_PARAMS':
   return {
     mediaType: action.payload.mediaType,
     inputs: {
       ...prevState.inputs,
       ...action.payload.inputs
     },
     booleans: {
       ...prevState.inputs,
       ...action.payload.booleans
     }
   }
  default:
   return prevState
 }
};
