import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { Card } from 'react-bootstrap';
import { Light as SyntaxHighlighter } from 'react-syntax-highlighter';
import js from 'react-syntax-highlighter/dist/cjs/languages/hljs/javascript';
import xml from 'react-syntax-highlighter/dist/cjs/languages/hljs/xml';
import ghgist from 'react-syntax-highlighter/dist/cjs/styles/hljs/github-gist';
import copy from 'copy-to-clipboard';

import Snackbar from '../Snackbar';
import classes from './CodeSnippetBlock.module.scss';

SyntaxHighlighter.registerLanguage('javascript', js);
SyntaxHighlighter.registerLanguage('xml', xml);

const CodeSnippetBlock = ({
  language,
  value,
  theme = {},
}) => {

  const snackbarRef = useRef();

  return (
    <div className={classes.container}>
      <Card padding="sm" style={{ border: 'none'}}>
        <pre className={classes.pre}>
          <SyntaxHighlighter className={classes.code} language={language} style={ghgist}>
            {value}
          </SyntaxHighlighter>
        </pre>
      </Card>
      <span
        className={classes.copy}
        onClick={(e) => {
          e.preventDefault();
          copy(value);
          snackbarRef.current.openSnackBar('Copied to clipboard.');
        }}
      >
        Copy
      </span>
      <Snackbar ref={snackbarRef} />
    </div>
  )
};

CodeSnippetBlock.propTypes = {
  value: PropTypes.string,
  language: PropTypes.string,
};

CodeSnippetBlock.defaultProps = {
  language: 'javascript',
  value: '',
};

export default CodeSnippetBlock;
