export const getHtmlCode = ({
  mediaType,
  inputs: {
    apiKey,
    assetsPerPage,
    htmlElement,
    widgetSubtitle,
    widgetTitle,
  },
  booleans: {
    dynamicSubtitle,
    dynamicTitle,
    showMore,
    showSearchBar,
  }
}) => `
<html>
  <head>
    <link rel="stylesheet" type="text/css" href="https://api-cdn.shutterstock.com/static/css/sstk-widget.css">
  </head>
  <body>
    <div id="${htmlElement}"></div>
    <script>
      window.onload = () => {
        const widget = new ShutterstockWidget({
          container: document.getElementById('${htmlElement}'),
          dynamicTitle: ${dynamicTitle},
          dynamicSubtitle: ${dynamicSubtitle},
          key: '${apiKey}',
          mediaType: '${mediaType}',
          showMore: true,
          showSearchBar: ${showSearchBar},
          title: '${widgetTitle}',
          subtitle: '${widgetSubtitle}',
          assetsPerPage: ${assetsPerPage}
        });

        widget.search({
          query: ''
        });
      }
    </script>
    <script src="https://api-cdn.shutterstock.com/static/js/sstk-widget.js"></script>
  </body>
</html>
`;
