import React from 'react';
import { Collapse } from 'react-bootstrap';
import { ChevronDown, ChevronUp } from 'react-bootstrap-icons';

const ExapandableList = ({
  heading,
  children,
  open,
  classes,
  onClick
}) => {
  return (
    <div className={classes.borderBottom}>
      <div className={classes.containerWithIcon} onClick={onClick}>
        <h6 className={classes.heading}>{heading}</h6>
        {open
          ? <ChevronUp color="black" size={15}/>
          : <ChevronDown color="black" size={15}/>
        }

      </div>
      <Collapse in={open}>
        <div>
          {children}
        </div>
      </Collapse>
    </div>
  );
};

export default ExapandableList;
