import React from 'react';
import useSearchWidget from './useSearchWidget';
import classes from './SearchWidget.module.scss';

const SearchWidget = ({
  mediaType,
  inputs: {
    apiKey,
    assetsPerPage,
    htmlElement,
    widgetSubtitle,
    widgetTitle,
  },
  booleans: {
    dynamicSubtitle,
    dynamicTitle,
    showMore,
    showItemId,
    showSearchBar,
  }
}) => {
  const { widgetRef } = useSearchWidget({
    apiKey,
    assetsPerPage,
    dynamicSubtitle,
    dynamicTitle,
    htmlElement,
    mediaType,
    showItemId,
    showMore,
    showSearchBar,
    widgetSubtitle,
    widgetTitle,
  });

  return (
    <div className={classes.container}>
      <div ref={widgetRef} />
    </div>
  );
};

export default SearchWidget;
