import React from 'react';
import { Form } from 'react-bootstrap';

const RadioButton = ({
  label,
  name,
  id,
  value,
  checked,
  defaultChecked = false
}) => {
  return (
    <Form.Check type="radio" label={label} name={name} id={id} value={value} checked={checked} defaultChecked={defaultChecked}/>
  );
};

export default RadioButton;
