import React, { useState } from 'react';
import { Form, Button, InputGroup, FormControl } from 'react-bootstrap';

import RadioButton from '../RadioButton';
import Checkbox from '../Checkbox';
import Input from '../Input';
import ExapandableList from '../ExapandableList';
import classes from './LeftNavMenu.module.scss';

const LeftNavMenu = ({
  mediaType: mediaTypeFromState,
  inputs,
  booleans,
  setParams
}) => {
  const [mediaType, setMediaType] = useState(mediaTypeFromState);
  const [apiKey, setApiKey] = useState(inputs.apiKey);
  const [htmlElement, setHtmlElement] = useState(inputs.htmlElement);
  const [widgetSubtitle, setWidgetSubtitle] = useState(inputs.widgetSubtitle);
  const [widgetTitle, setWidgetTitle] = useState(inputs.widgetTitle);

  const [dynamicSubtitle, setDynamicSubtitle] = useState(booleans.dynamicSubtitle);
  const [dynamicTitle, setDynamicTitle] = useState(booleans.dynamicTitle);
  const [showMore, setShowMore] = useState(booleans.showMore);
  const [showSearchBar, setShowSearchBar] = useState(booleans.showSearchBar);

  const [assetsPerPage, setAssetsPerPage] = useState(inputs.assetsPerPage);

  const [open, setOpen] = useState({
    options: true,
    mediaType: true,
    htmlContainer: true,
    apiKey: true,
    titleAndSubtitle: true,
  });

  return (
    <div className={classes.container} style={{ marginTop: 20 }}>
      <ExapandableList
        heading="Select media type"
        classes={classes}
        open={open.mediaType}
        onClick={() => setOpen({ ...open, mediaType: !open.mediaType })}
      >
        <Form.Group onChange={(e) => setMediaType(e.target.value)}>
          <RadioButton label="Images" name="mediaType" id="images" value="images" defaultChecked={mediaType === "images"} />
          <RadioButton label="Videos" name="mediaType" id="videos" value="videos" defaultChecked={mediaType === "videos"} />
        </Form.Group>
      </ExapandableList>

      <ExapandableList
        heading="Title & Subtitle"
        classes={classes}
        open={open.titleAndSubtitle}
        onClick={() => setOpen({ ...open, titleAndSubtitle: !open.titleAndSubtitle })}
      >
        <h6>Title</h6>
        <Input value={widgetTitle} onChange={(e) => setWidgetTitle(e.target.value)} />
        <h6>Subtitle</h6>
        <Input value={widgetSubtitle} onChange={(e) => setWidgetSubtitle(e.target.value)} />
        <small className="form-text text-muted">Enter custom text (uncheck dynamic titles)</small>
      </ExapandableList>

      <ExapandableList
        heading="Options"
        classes={classes}
        open={open.options}
        onClick={() => setOpen({ ...open, options: !open.options })}
      >
        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text>Assets per page</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            aria-label="assets-per-page"
            value={assetsPerPage}
            onChange={(e) => setAssetsPerPage(e.target.value)}
          />
        </InputGroup>
        <Checkbox label="Show search bar" value="showSearchBar" defaultChecked={showSearchBar} onChange={(e) => setShowSearchBar(e.target.checked)} />
        <Checkbox label="Show more button" value="showMore" defaultChecked={showMore} onChange={(e) => setShowMore(e.target.checked)} />
        <Checkbox label="Use dynamic title" value="dynamicTitle" defaultChecked={dynamicTitle} onChange={(e) => setDynamicTitle(e.target.checked)} />
        <Checkbox label="Use dynamic subtitle" value="dynamicSubtitle" defaultChecked={dynamicSubtitle} onChange={(e) => setDynamicSubtitle(e.target.checked)} />
      </ExapandableList>

      <ExapandableList
        heading="HTML container"
        classes={classes}
        open={open.htmlContainer}
        onClick={() => setOpen({ ...open, htmlContainer: !open.htmlContainer })}
      >
        <Input value={htmlElement} onChange={(e) => setHtmlElement(e.target.value)} />
        <small className="form-text text-muted">Element you want the widget to render in.</small>
      </ExapandableList>

      <ExapandableList
        heading="API key"
        classes={classes}
        open={open.apiKey}
        onClick={() => setOpen({ ...open, apiKey: !open.apiKey })}
      >
        <Input value={apiKey} onChange={(e) => setApiKey(e.target.value)} />
        <small className="form-text text-muted">Example - use your own API key in the integration.</small>
      </ExapandableList>

      <div className="mt-4 mb-5 text-center">
        <Button className="btn btn-danger" style={{ borderRadius: '25px' }} onClick={() => {
          setParams({
            mediaType,
            inputs: {
              apiKey,
              htmlElement,
              widgetSubtitle,
              widgetTitle,
              assetsPerPage,
            },
            booleans: {
              dynamicSubtitle,
              dynamicTitle,
              showMore,
              showSearchBar,
            },
          });
        }}>Load widget</Button>
      </div>
    </div>
  );
};

export default LeftNavMenu;
