import React from 'react';
import { InputGroup, FormControl } from 'react-bootstrap';

const Input = ({
  label,
  value,
  onChange
}) => {
  return (
    <InputGroup size="sm" className="mb-3">
      <FormControl aria-label="Small" aria-describedby="inputGroup-sizing-sm" onChange={onChange} value={value} />
    </InputGroup>
  );
};

export default Input;
